<template>
  <div v-if="!mintEnabled" class=" d-flex justify-content-center pt-5">
    <h1
      v-for="(time, index) in times"
      :key="index"
      class="time d-flex text-primary"
    >
      <span v-if="index > 0" class="px-3">:</span>
      <div class="d-flex justify-content-center" style="width: 40px;">
        {{ time }}
      </div>
    </h1>
  </div>
</template>

<script>
import dayjs from "dayjs";
import utc from "dayjs/plugin/utc";
import timezone from "dayjs/plugin/timezone";

import { MINT_ENABLED } from "@/constants";

dayjs.extend(utc);
dayjs.extend(timezone);

dayjs.tz.setDefault("America/New_York");

export default {
  name: "countdown",
  data() {
    return {
      difference: null,
      times: [],
    };
  },
  mounted() {
    const end = dayjs.tz("2021-09-18 00:00");

    this.difference = end.diff(dayjs.tz());

    this.updateCountdown();

    setInterval(() => {
      this.updateCountdown();
    }, 1000);
  },
  computed: {
    mintEnabled() {
      return MINT_ENABLED;
    },
  },
  methods: {
    format(value) {
      return value.toString().padStart(2, "0");
    },
    updateCountdown() {
      this.difference = this.difference - 1000;

      const times = [];
      times[0] = this.format(
        Math.floor(this.difference / (1000 * 60 * 60 * 24))
      );
      times[1] = this.format(
        Math.floor((this.difference / (1000 * 60 * 60)) % 24)
      );
      times[2] = this.format(Math.floor((this.difference / 1000 / 60) % 60));
      times[3] = this.format(Math.floor((this.difference / 1000) % 60));

      this.times = times;
    },
  },
};
</script>

<style lang="scss" scoped>
@import "@/assets/theme/custom.scss";

.time {
  color: $primary;
  text-shadow: -1px 1px 2px #000, 1px 1px 2px #000, 1px -1px 0 #000,
    -1px -1px 0 #000;
}
</style>
