<template>
  <div style="width: 300px;">
    <b-button
      variant="outline-primary"
      class="rounded-0 w-100"
      @click="connectWallet"
      >{{ walletMessage }}</b-button
    >
    <vue-slider
      v-model="mintCount"
      class="mt-5 mb-4"
      marks
      hideLabel
      :min="1"
      :max="15"
      :duration="0"
      :disabled="!address"
    />
    <b-button
      variant="primary"
      class="mint-button rounded-0 w-100"
      :disabled="!address || !mintEnabled"
      @click="mint"
      >{{ mintButtonMessage }}</b-button
    >
    <h6
      v-if="address && amountMinted && mintEnabled"
      class="d-flex justify-content-between mt-4 text-primary"
    >
      <span>Total minted:</span><strong>{{ amountMinted }}/5000</strong>
    </h6>
    <countdown />
  </div>
</template>

<script>
import { CONTRACT_ABI, CONTRACT_ADDRESS, MINT_ENABLED } from "@/constants";
import VueSlider from "vue-slider-component";
import Countdown from "./Countdown.vue";

import { ethers } from "ethers";

export default {
  name: "mint",
  components: {
    VueSlider,
    Countdown,
  },
  data() {
    return {
      provider: null,
      signer: null,
      address: "",
      mintCount: 1,
      contract: null,
      amountMinted: null,
    };
  },
  computed: {
    mintEnabled() {
      return MINT_ENABLED;
    },
    walletMessage() {
      return this.address
        ? `Connected: ${this.address.substring(0, 8)}...`
        : "Connect Wallet";
    },
    mintButtonMessage() {
      const soonQualifier = this.mintEnabled ? "" : " (soon...)";
      return this.mintCount > 1
        ? `Mint ${this.mintCount} WojakPunks!${soonQualifier}`
        : `Mint ${this.mintCount} WojakPunk!${soonQualifier}`;
    },
  },
  async mounted() {
    this.provider = new ethers.providers.Web3Provider(window.ethereum);
    this.contract = new ethers.Contract(
      CONTRACT_ADDRESS,
      CONTRACT_ABI,
      this.provider
    );
    this.amountMinted = await this.contract.totalSupply();
  },
  methods: {
    async connectWallet() {
      await this.provider.send("eth_requestAccounts", []);
      this.signer = this.provider.getSigner();
      this.address = await this.signer.getAddress();
    },
    async mint() {
      const contractWithSigner = await this.contract.connect(this.signer);

      const ethToSend = (0.025 * this.mintCount).toFixed(3);

      contractWithSigner.mint(this.mintCount, {
        value: ethers.utils.parseEther(ethToSend.toString()),
      });
    },
  },
};
</script>
