<template>
  <div id="container">
    <div class="scroll-footer">
      <img
        v-for="(id, index) in footerImageIds"
        :src="getFooterImagePath(id)"
        :key="index"
        class="image-width"
      />
    </div>
  </div>
</template>

<script>
export default {
  name: "page-footer",
  computed: {
    footerImageIds() {
      const footerImageIds = Array.from(Array(7).keys());
      return [...footerImageIds, ...footerImageIds];
    },
  },
  methods: {
    getFooterImagePath(id) {
      return require(`@/assets/images/wojakpunk-footer-${id}.png`);
    },
  },
};
</script>

